<template>
    <div class="max-w-md mx-auto flex flex-col justify-center items-center">
        <h1 class="title">Find Your Sound</h1>
        <div class="my-6">
        <button @click="startTimer" ref="startButton"
                class="start-button w-24 h-24 rounded-full text-2xl text-white font-bold">
            Start
        </button>
        </div>
    </div>
</template>

<script>
  import * as Tone from "tone";

  export default {
    name: "start-screen",
    methods: {
      async startTimer() {
        await Tone.start();
        this.$emit('start');
      }
    },
    mounted() {
    }
  };
</script>

<style scoped>
    .start-button {
        @apply bg-blue-500;
    }
    .start-button:active {
        @apply bg-yellow-500;
    }
</style>
