<template>
    <div id="app" class="min-h-screen flex justify-center items-center">
        <start-screen v-if="state === 'init'" @start="onStart"></start-screen>
        <collect-data v-if="state === 'collect'" @data="onData"></collect-data>
        <show-data v-if="state === 'finish'" :points="dataPoints" @restart="onRestart"></show-data>
    </div>
</template>

<script>
  import StartScreen from "@/components/start-screen";
  import CollectData from "@/components/collect-data";
  import ShowData from "@/components/show-data";

  export default {
    name: 'App',
    data() {
      return {
        state: 'init',
        dataPoints: []
      };
    },
    components: {
      ShowData,
      CollectData,
      StartScreen
    },
    methods: {
      onStart() {
        this.state = 'collect';
      },
      onData(data) {
        this.state = 'finish';
        this.dataPoints = data;
      },
      onRestart() {
        this.state = 'init';
        this.dataPoints = [];
      }
    }
  };
</script>

<style>
    .title {
        @apply text-4xl font-hairline mb-6;
    }
</style>
