<template>
    <div>
        <div class="flex flex-col justify-center items-center">
            <div class="chart-container" style="position: relative; height:40vh; width:80vw">
                <canvas style="width: 100%" class="block my-6 w-full" ref="chart"></canvas>
            </div>
            <div class="flex flex-col items-center z-10">
            <button @click="play" class=" play-button rounded-full w-16 h-16 text-white font-bold">
                Play
            </button>
            <button class="mt-6 bg-gray-300 px-4 py-2 rounded font-bold" @click="restart">Restart</button>
            </div>
        </div>
    </div>
</template>

<script>
  import * as Chart from 'chart.js';
  import * as Tone from "tone";

  export default {
    name: "show-data",
    props: ['points'],
    data() {
      return {
        chart: null,
        synth: null,
        seq: null
      };
    },
    methods: {
      restart() {
        this.seq.dispose();
        Tone.Transport.cancel();
        this.$emit('restart');
      },
      async play() {
        Tone.Transport.stop();
        Tone.Transport.start();
      }
    },
    mounted() {
      this.synth = new Tone.Synth().toMaster();
      const notes = [
        'C4',
        'D4',
        'E4',
        'F4',
        'G4',
        'A4',
        'C5'
      ];
      const toPlay = this.points.map(({ value }) => notes[value + 3]);
      console.log({ toPlay, data: this.points });
      this.seq = new Tone.Sequence((time, note) => {
        this.synth.triggerAttackRelease(note, "8n", time);
      }, toPlay, "8n");
      this.seq.start(0);
      this.seq.loop = false;

      this.chart = new Chart(this.$refs.chart, {
        type: 'scatter',
        data: {
          datasets: [{
            label: 'Emotion',
            borderColor: 'rgba(255, 99, 132, 0.2)',
            backgroundColor: 'rgba(255, 99, 132, 1)',
            data: this.points.map(({time, value}) => ({x: time, y: (-value) + 3}))
          }]
        },
        options: {
          legend: {
            display: false
          },
          tooltips: {
            enabled: false
          },
          scales: {
            xAxes: [{
              type: 'linear',
              position: 'bottom',
              scaleLabel: {
                display: true,
                labelString: 'Time'
              },
              ticks: {
                min: 0
              }
            }],
            yAxes: [{
              type: 'category',
              labels: [
                'Orgasmic',
                'Laughing',
                'Smiling',
                'Meh',
                'Confused',
                'Morose',
                'Suicidal'
              ],
              scaleLabel: {
                display: true,
                labelString: 'Emotion'
              }
            }]
          }
        }
      });
    }
  };
</script>

<style scoped>
    .play-button {
        @apply bg-blue-500;
    }
    .play-button:active {
        @apply bg-yellow-500;
    }
</style>
