<template>
    <div class="max-w-md mx-auto flex flex-col items-center justify-center">
        <h1 class="title">How are you feeling?</h1>
        <div class="mb-4 timer">{{ timeFormat }}</div>
        <div class="flex flex-col mt-2 emotion-buttons">
            <button @click="recordPoint(3)">Orgasmic</button>
            <button @click="recordPoint(2)">Laughing</button>
            <button @click="recordPoint(1)">Smiling</button>
            <button @click="recordPoint(0)">Meh</button>
            <button @click="recordPoint(-1)">Confused</button>
            <button @click="recordPoint(-2)">Morose</button>
            <button @click="recordPoint(-3)">Suicidal</button>
        </div>
        <div class="mt-6">
            <button class="bg-gray-300 px-4 py-2 rounded" @click="stopTimer">Stop</button>
        </div>
    </div>
</template>

<script>
  import moment from 'moment';

  export default {
    name: "collect-data",
    data() {
      return {
        time: 0,
        timerInterval: null,
        dataPoints: []
      };
    },
    computed: {
      timeFormat() {
        const t = moment.duration(this.time, 'seconds');
        return `${t.hours().toString(10).padStart(2, '0')}:${t.minutes().toString(10).padStart(2, '0')}:${t.seconds().toString(10).padStart(2, '0')}`;
      }
    },
    methods: {
      recordPoint(value) {
        this.dataPoints.push({time: this.time, value});
      },
      stopTimer() {
        clearInterval(this.timerInterval);
        this.$emit('data', this.dataPoints);
      }
    },
    mounted() {
      this.timerInterval = setInterval(() => {
        this.time += 0.1;
      }, 100);
    }
  };
</script>

<style scoped>
    .emotion-buttons > button {
        @apply bg-blue-500 mb-4 px-4 py-2 rounded text-white font-bold;
    }
    .emotion-buttons > button:hover {
        @apply bg-blue-700;
    }
    .emotion-buttons > button:active {
        @apply bg-yellow-500;
    }

    .timer {
        font-variant-numeric: tabular-nums;
    }
</style>
